const namespaced = true;
import axios from 'axios'
const state={
    myCourse:[{
        courseInfoId:1,
        serial:1,
        courseName:''
    },{
        courseInfoId:2,
        serial:2,
        courseName:''
    },{
        courseInfoId:3,
        serial:3,
        courseName:''
    }],
    myClass:['1715431','1715432','1715433'],
    courseFile:[{
        courseFolderId:1,
        courseName:'',
        courseFolderName:'',
        courseClass:'1715431'
    },{
        courseFolderId:2,
        courseName:'',
        courseFolderName:'',
        courseClass:'1715431'
    },{
        courseFolderId:3,
        courseName:'',
        courseFolderName:'',
        courseClass:'1715431'
    },{
        courseFolderId:4,
        courseName:'',
        courseFolderName:'',
        courseClass:'1715433'
    },],
    courseInfo: {
        name: "java",
        teachingDocument: 1,
        teachingCase: 0,
        teachingResources: 1,
        otherTeachingResources: 1,
        experimentalReportTemplate: 1,
        dailyWorkTemplate: 1,
        otherTermAssignments: 0
    },
    detailFile:[{
        courseFileName: "1",
        courseFileId: ""
    },{
        courseFileName: "1",
        courseFileId: ""
    },{
        courseFileName: "1",
        courseFileId: ""
    },]
}
const getters={
    getCourseInfo(state) {
        return state.courseInfo
    },
    getMyCourse(state){
        return state.myCourse
    },
    getCourseFile(state){
        return state.courseFile
    },
    getMyClass(state){
        return state.myClass
    },
    getDetailFile(state){
        return state.detailFile
    }
}
const mutations={
    setMyCourse(state,data){
        state.myCourse=data;
        for(let i=0;i<state.myCourse.length;i++){
            state.myCourse[i].serial=i+1;
        }
    },
    setCourseFile(state,data){
        
        state.courseFile=data;
    },
    changeInfo(state,{data,content}){
        state.courseInfo = data;
    },
    changeDetail(state,data){
        state.detailFile=data;
    }
}
const actions={
    setTid({commit},id){
        commit("setId",id);
    },
    // 
    requestMyCourse({commit}){
        axios.get(`/teacher/courseInfo/findListMyCourse`)
            .then(res=>{
                commit("setMyCourse",res.data.data)
            }).catch(err=>{

            })
    },
    // 
    submitChangeCourse({state,dispatch},{content,courseName,that}){
        axios.put(`/teacher/courseInfo/updateCourse?courseInfoId=${parseInt(content.courseInfoId)}&courseName=${courseName}`)
            .then(res=>{
                this.$message({
                    type: 'success',
                    message: ': ' + content.courseName
                });
                // 
                dispatch('requestMyCourse')
            }).catch(err=>{
                that.$message({
                    type: 'error',
                    message: ''
                });
            })
    },
    // 
    submitDeleteCourse({state,dispatch},{content,that}){
        axios.delete(`/teacher/courseFile/deleteCourse?courseInfoId=${[content.courseInfoId]}`)
            .then(res=>{
                that.$message({
                    type: 'success',
                    message: res.data.message
                }); 
            }).catch(err=>{
                that.$message({
                    type: 'error',
                    message: ''
                });
            })
            dispatch('requestMyCourse')
    },
    // 
    submitCreateClamp({dispatch},{name,classNo,id,that}){
        classNo=classNo.split(',');
        axios.post(`/teacher/courseFolder/createFolder?folderName=${name}&courseClass=${classNo}&courseInfoId=${id}`)
            .then(res=>{
                if((res.data.code>=200&&res.data.code<300)||res.data.code==304){
                    that.$message({
                        type: 'success',
                        message: res.data.message
                    }); 
                }else{
                    that.$message({
                        type: 'error',
                        message: res.data.message
                    }); 
                }
            }).catch(err=>{
                that.$message({
                    type: 'error',
                    message: ''
                });
            })
    },
    // 
    requestFolder({commit,state}){
        axios.get(`/teacher/courseInfo/findListFolder`)
            .then(res=>{
                
                commit('setCourseFile',res.data)
            }).catch(err=>{

            })
    },
    // // 
    requestCourseDetail({commit},{content,type}){
        console.log('content: ', content);
        // 0-> 1-> 2-> 3->
        axios.get(`/student/courseFile/read?courseInfoId=${content.courseInfoId}`)
            .then(res=>{
                console.log('res.data: ', res.data);
                commit('changeInfo', {data:res.data,content})
            }).catch(err=>{

            })
    },
    // 
    requestDetail({commit},{id,type,that}){
        console.log('id: ', id);
        axios.get(`/student/courseFile/to?courseInfoId=${id}&courseFileType=${type}`)
            .then(res=>{
                commit('changeDetail',res.data.data);
            }).catch(err=>{

            })
        },
    // 
    changeFolderData({dispatch},{content,courseFileName,that}){
        axios.put(`/teacher/courseFile/updateCourseFile?courseFileId=${content.courseFileId}&courseFileName=${courseFileName}`)
            .then(res=>{
                that.$message({
                    type: 'success',
                    message: ''
                }); 
            }).catch(err=>{
                that.$message({
                    type: 'error',
                    message: ''
                }); 
            })
            dispatch("requestMyCourse")
    },
    // 
    deleteFolderData({state,dispatch},{content,that}){
        axios.delete(`/teacher/courseFile/deleteDocument?idList=${[content.courseFileId]}`)
            .then(res=>{
                if((res.data.code>=200&&res.data.code<300)||res.data.code==304){
                    that.$message({
                        type:'success',
                        message:res.data.msg
                    })
                }else{
                    that.$message({
                        type:'error',
                        message:''
                    })
                }
            }).catch(err=>{
                that.$message({
                    type:'error',
                    message:''
                })
            })
        dispatch("requestMyCourse")
    },
    // 
    deleteServalFolderData({state,dispatch},{content,that}){
        let idList=[];
        for(let i=0;i<content.length;i++){
            idList.push(content.courseFileId)
        }
        axios.delete(`/teacher/courseFile/deleteDocument?idList=${idList}`)
        .then(res=>{
            if((res.data.code>=200&&res.data.code<300)||res.data.code==304){
                that.$message({
                    type:'success',
                    message:res.data.msg
                })
            }else{
                that.$message({
                    type:'error',
                    message:''
                })
            }
        }).catch(err=>{
            that.$message({
                type:'error',
                message:''
            })
        })
        dispatch("requestMyCourse")
    },
    // 
    downloadCourseFolder({state},{content,that}){
        let courseFolderName=content.courseClass+content.courseFolderName;
        axios({
            method:'get',
            url:`/teacher/courseFile/downloadHomewordFile?courseFolderId=${parseInt(content.courseFolderId)}&courseFolderName=${courseFolderName}`,
            responseType: 'blob'
        }).then(res=>{
            if(res.data){
                const a = document.createElement('a');
                a.download = `${courseFolderName}.zip`;
                a.href =`/teacher/courseFile/downloadHomewordFile?courseFolderId=${parseInt(content.courseFolderId)}&courseFolderName=${courseFolderName}`;
                document.body.appendChild(a);
                a.click();
                document.body.removeChild(a);
                that.$message({
                    type: 'success',
                    message: ''
                }); 
            }else{
                that.$message({
                    type: 'error',
                    message: ''
                });    
            }
        })
    }
}

export default {namespaced,state,getters,mutations,actions}